*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

@media screen and (max-width: 37.5em) {
  html {
    font-size: 50%;
  }
}

body {
  box-sizing: border-box;
  font-family: Overpass, sans-serif;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1;
}

.rating__submit-btn {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 3px;
  cursor: pointer;
  background-color: #fb7413;
  border: none;
  border-radius: 100rem;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: 700;
  transition: all .3s;
  display: block;
}

.rating__submit-btn:hover, .rating__submit-btn:active {
  color: #fb7413;
  background-color: #fff;
}

.rating {
  width: 40rem;
  height: 40rem;
  background-color: #252d37;
  border-radius: 2rem;
  padding: 3rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 25em) {
  .rating {
    width: 90%;
  }
}

.rating__form {
  height: 100%;
  display: grid;
}

.rating__icon {
  width: 5rem;
  background-color: #303a46;
  border-radius: 50%;
  padding: 1.5rem;
}

.rating__title {
  color: #fff;
  align-self: center;
  font-size: 3rem;
}

.rating__text {
  color: #959eac;
  line-height: 1.5;
}

.rating__fieldset {
  border: none;
  justify-content: space-between;
  display: flex;
}

.rating__label {
  width: 5rem;
  height: 5rem;
  cursor: pointer;
  color: #7c8798;
  background-color: #303a46;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  transition: all .3s;
  display: flex;
}

.rating__label:hover {
  color: #fff;
  background-color: #7c8798;
}

.rating__input {
  display: none;
}

.rating__input:checked + .rating__label {
  color: #fff;
  background-color: #fb7413;
}

.rating__submitted {
  text-align: center;
  height: 100%;
  place-items: center;
  display: grid;
}

.rating__selected {
  color: #fb7413;
  background-color: #303a46;
  border-radius: 100rem;
  padding: 1rem 1.7rem .8rem;
}

.rating__submitted .rating__title {
  margin-bottom: -1rem;
}

.content {
  height: 100vh;
  background-color: #121417;
  position: relative;
}

.hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
  height: 1px;
  white-space: nowrap;
  width: 1px;
  position: absolute;
  overflow: hidden;
}

